









import Vue from 'vue'
import SystemName from '@/system/system-name'
export default Vue.extend({
    data:function(){
        return {
            title:"User Info"
        }
    },
    created:function(){
        Vue.prototype.$f_userName = "MyName"
    },
    computed:{
        userName:function(){
            return SystemName.user;
        },
        icon:function(){
            return require("./icon.svg");
        }
    }
})
